import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { Formik, Form, Field } from 'formik'
import { Switch, TextField } from 'formik-material-ui'
import * as Yup from 'yup'
import {
  Button,
  Fab,
  Grid,
  Paper,
  Typography,
  makeStyles,
} from '@material-ui/core'
import { SaveAlt } from '@material-ui/icons'

import ControlledSwitch from './ControlledSwitch'
import ListErrors from './ListErrors'
import PageHeader from './PageHeader'
// import SpotifyLinker from './Teacher/SpotifyLinker'
import agent from '../agent'

import {
  SETTINGS_PAGE_UNLOADED,
  SETTINGS_SAVED,
  WHATS_NEW_DIALOG_LOADED,
} from '../constants/actionTypes'

const useStyles = makeStyles((theme) => ({
  icon: {
    marginRight: theme.spacing(1),
  },
  paper: {
    padding: theme.spacing(2),
    marginBottom: theme.spacing(4),
  },
}))

const mapStateToProps = (state) => ({
  ...state.settings,
  currentUser: state.common.currentUser,
  settings: state.common.settings,
})

const mapDispatchToProps = (dispatch) => ({
  onSubmitForm: (settings) =>
    dispatch({
      type: SETTINGS_SAVED,
      payload: Promise.all([
        agent.Auth.save(settings),
        agent.Settings.update(settings),
      ]),
      snackbar: { message: 'Settings saved', variant: 'success' },
    }),
  onUnload: () => dispatch({ type: SETTINGS_PAGE_UNLOADED }),
  onWhatsNewClick: () =>
    dispatch({
      type: WHATS_NEW_DIALOG_LOADED,
      payload: agent.Auth.save({ viewedChanges: false }),
    }),
})

const Settings = ({
  currentUser,
  errors,
  inProgress,
  onSubmitForm,
  onUnload,
  onWhatsNewClick,
  settings,
}) => {
  const classes = useStyles()

  useEffect(() => {
    return () => {
      onUnload()
    }
  }, [])

  return (
    <Formik
      enableReinitialize={true}
      initialValues={{
        allowPurchasesWithInsufficientFunds:
          settings?.allowPurchasesWithInsufficientFunds || false,
        defaultStudentPassword: settings?.defaultStudentPassword || '',
        enableBetaFeatures: settings?.enableBetaFeatures || false,
        firstName: currentUser.firstName,
        lastName: currentUser.lastName,
        password: '',
        productsEnabled: settings?.productsEnabled || false,
        score100: settings?.scoreConverter?.[100] || 1200,
        score90: settings?.scoreConverter?.[90] || 1000,
        score80: settings?.scoreConverter?.[80] || 800,
        score70: settings?.scoreConverter?.[70] || 600,
        score60: settings?.scoreConverter?.[60] || 0,
        score50: settings?.scoreConverter?.[50] || 0,
        songRemovalPurchaseAmount: settings?.songRemovalPurchaseAmount || 0,
        songRemovalRequestEnabled: settings?.songRemovalRequestEnabled || false,
        songRemovalRefundAmount: settings?.songRemovalRefundAmount || 0,
        songRequestEnabled: settings?.songRequestEnabled || false,
        songRequestPurchaseAmount: settings?.songRequestPurchaseAmount || 0,
        storeEnabled: settings?.storeEnabled || false,
        taxRate: settings?.taxRate || 0,
      }}
      validationSchema={Yup.object({
        allowPurchasesWithInsufficientFunds: Yup.boolean(),
        defaultStudentPassword: Yup.string(),
        enableBetaFeatures: Yup.boolean(),
        firstName: Yup.string(),
        lastName: Yup.string(),
        password: Yup.string(),
        productsEnabled: Yup.boolean(),
        score100: Yup.number().typeError('Numbers only'),
        score90: Yup.number().typeError('Numbers only'),
        score80: Yup.number().typeError('Numbers only'),
        score70: Yup.number().typeError('Numbers only'),
        score60: Yup.number().typeError('Numbers only'),
        score50: Yup.number().typeError('Numbers only'),
        songRemovalPurchaseAmount: Yup.number().typeError('Numbers only'),
        songRemovalRequestEnabled: Yup.boolean(),
        songRemovalRefundAmount: Yup.number().typeError('Numbers only'),
        songRequestEnabled: Yup.boolean(),
        songRequestPurchaseAmount: Yup.number().typeError('Numbers only'),
        storeEnabled: Yup.boolean(),
        taxRate: Yup.number().typeError('Numbers only'),
      })}
      onSubmit={async (values, form) => {
        const userSettings = { ...values }

        if (!userSettings.password && currentUser.role !== 'student')
          delete userSettings.password

        if (currentUser.role !== 'student')
          userSettings.scoreConverter = {
            100: values.score100,
            90: values.score90,
            80: values.score80,
            70: values.score70,
            60: values.score60,
            50: values.score50,
            40: 0,
            30: 0,
            20: 0,
            10: 0,
            0: 0,
          }

        onSubmitForm(userSettings)

        form.resetForm({
          values: { ...userSettings, password: '' },
        })
      }}
    >
      {(formik) => (
        <Form>
          <PageHeader title='Manage Account'>
            <Button onClick={onWhatsNewClick} size='small'>
              See What's New
            </Button>
            <Fab
              color='primary'
              disabled={inProgress}
              type='sumbit'
              variant='extended'
            >
              <SaveAlt className={classes.icon} />
              {inProgress ? 'Saving...' : 'Save settings'}
            </Fab>
          </PageHeader>
          <ListErrors errors={errors} />

          {currentUser.role === 'teacher' && (
            <Paper className={classes.paper}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <Typography variant='h5' gutterBottom>
                    Song Requests
                  </Typography>
                </Grid>
                <Grid item xs={12} md={4}>
                  <label>
                    <Field
                      component={Switch}
                      label='Allow song requests'
                      name='songRequestEnabled'
                      type='checkbox'
                    />
                    Allow song requests
                  </label>
                </Grid>
                <Grid item xs={12} md={4}>
                  <Field
                    component={TextField}
                    label='Song request purchase amount'
                    name='songRequestPurchaseAmount'
                    type='number'
                    variant='outlined'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12}>
                  <Typography variant='h6' gutterBottom>
                    Song Removal Requests
                  </Typography>
                </Grid>
                <Grid item xs={12} md={4}>
                  <label>
                    <Field
                      component={Switch}
                      label='Allow students to request song removals'
                      name='songRemovalRequestEnabled'
                      type='checkbox'
                    />
                    Allow song removal requests
                  </label>
                </Grid>
                <Grid item xs={12} md={4}>
                  <Field
                    component={TextField}
                    label='Song removal purchase amount'
                    name='songRemovalPurchaseAmount'
                    type='number'
                    variant='outlined'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} md={4}>
                  <Field
                    component={TextField}
                    label='Song removal refund amount'
                    name='songRemovalRefundAmount'
                    type='number'
                    variant='outlined'
                    fullWidth
                  />
                </Grid>
                {/* <Grid item xs={12} md={6}>
                <SpotifyLinker />
              </Grid> */}
              </Grid>
            </Paper>
          )}

          {currentUser.role === 'teacher' && (
            <Paper className={classes.paper}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <Typography variant='h5' gutterBottom>
                    {formik.values.enableBetaFeatures
                      ? 'Products and Online Purchases'
                      : 'Products'}
                  </Typography>
                </Grid>
                {formik.values.enableBetaFeatures ? (
                  <>
                    <Grid item xs={12} md={6}>
                      <label>
                        <Field
                          component={Switch}
                          label='Enable online purchases'
                          name='storeEnabled'
                          type='checkbox'
                        />
                        Allow online purchases
                      </label>
                    </Grid>
                    <Grid item xs={12} md={4}>
                      <Field
                        component={TextField}
                        label='Tax rate'
                        name='taxRate'
                        type='number'
                        variant='outlined'
                        fullWidth
                      />
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <label>
                        <ControlledSwitch
                          controlledBy={'storeEnabled'}
                          disabled={formik.values.storeEnabled}
                          label='Display the Products page'
                          name='productsEnabled'
                        />
                        Display the Products page
                        {formik.values.storeEnabled && (
                          <em> (required for purchases)</em>
                        )}
                      </label>
                    </Grid>
                    <Grid item xs={12} md={6}>
                      <label>
                        <Field
                          component={Switch}
                          label='Allow purchases with insufficient funds'
                          name='allowPurchasesWithInsufficientFunds'
                          type='checkbox'
                        />
                        Allow purchases with insufficient funds
                      </label>
                    </Grid>
                  </>
                ) : (
                  <Grid item xs={12} md={6}>
                    <label>
                      <Field
                        component={Switch}
                        label='Display the products page'
                        name='productsEnabled'
                        type='checkbox'
                      />
                      Display the Products page
                    </label>
                  </Grid>
                )}
              </Grid>
            </Paper>
          )}

          {currentUser.role === 'teacher' && (
            <Paper className={classes.paper}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <Typography variant='h5' gutterBottom>
                    Student Accounts
                  </Typography>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Field
                    component={TextField}
                    label='Default student password'
                    name='defaultStudentPassword'
                    type='text'
                    variant='outlined'
                    fullWidth
                  />
                </Grid>
              </Grid>
            </Paper>
          )}

          {currentUser.role === 'teacher' && (
            <Paper className={classes.paper}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <Typography variant='h5' gutterBottom>
                    Score Conversions
                  </Typography>
                </Grid>
                <Grid item xs={12} md={2}>
                  <Field
                    component={TextField}
                    label='100% and up'
                    name='score100'
                    type='number'
                    variant='outlined'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} md={2}>
                  <Field
                    component={TextField}
                    label='90% and up'
                    name='score90'
                    type='number'
                    variant='outlined'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} md={2}>
                  <Field
                    component={TextField}
                    label='80% and up'
                    name='score80'
                    type='number'
                    variant='outlined'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} md={2}>
                  <Field
                    component={TextField}
                    label='70% and up'
                    name='score70'
                    type='number'
                    variant='outlined'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} md={2}>
                  <Field
                    component={TextField}
                    label='60% and up'
                    name='score60'
                    type='number'
                    variant='outlined'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} md={2}>
                  <Field
                    component={TextField}
                    label='50% and up'
                    name='score50'
                    type='number'
                    variant='outlined'
                    fullWidth
                  />
                </Grid>
              </Grid>
            </Paper>
          )}

          {currentUser.role !== 'student' && (
            <Paper className={classes.paper}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <Typography variant='h5' gutterBottom>
                    Account Details
                  </Typography>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Field
                    component={TextField}
                    label='First name'
                    name='firstName'
                    type='text'
                    variant='outlined'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <Field
                    component={TextField}
                    label='Last name'
                    name='lastName'
                    type='text'
                    variant='outlined'
                    fullWidth
                  />
                </Grid>
                {currentUser.role === 'teacher' && (
                  <Grid item xs={12} md={4}>
                    <label>
                      <Field
                        component={Switch}
                        label='Enable beta features'
                        name='enableBetaFeatures'
                        type='checkbox'
                      />
                      Enable beta features
                    </label>
                  </Grid>
                )}
              </Grid>
            </Paper>
          )}

          <Paper className={classes.paper}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <Typography variant='h5' gutterBottom>
                  Account Security
                </Typography>
              </Grid>
              <Grid item xs={12} md={6}>
                <Field
                  component={TextField}
                  label='New password'
                  name='password'
                  type='password'
                  variant='outlined'
                  fullWidth
                />
              </Grid>
            </Grid>
          </Paper>
        </Form>
      )}
    </Formik>
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(Settings)
