export const useGetInvoice = (products, quantities, taxRate) => {
  if (products && quantities) {
    const items = products.map((product) => ({
      desc: product.name,
      imgUrl: product.photoUrl,
      max: product.maxPurchaseQuantity,
      onHand: product.quantity,
      price: quantities[product._id] * product.cost,
      qty: quantities[product._id],
      unit: product.cost,
      _id: product._id,
    }))

    const subtotal = items
      .map(({ price }) => price)
      .reduce((sum, i) => sum + i, 0)
    const taxes = (taxRate / 100) * subtotal
    const total = taxes + subtotal

    const invoice = { subtotal, taxes, total }

    return { items, invoice }
  } else {
    return {}
  }
}
