import React, { useState } from 'react'
import {
  Avatar,
  Card,
  CardContent,
  Chip,
  ExpansionPanel,
  ExpansionPanelDetails,
  ExpansionPanelSummary,
  Grid,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  makeStyles,
} from '@material-ui/core'
import {
  Assignment,
  ExpandMore,
  LocalAtm,
  Money,
  PlaylistAddCheck,
  ShoppingCart,
  Store,
} from '@material-ui/icons'

import TeacherDialog from './TeacherDialog'
import DeleteTeacherAlert from './DeleteTeacherAlert'

const useStyles = makeStyles((theme) => ({
  statAvatar: {
    backgroundColor: theme.palette.primary.main,
    height: theme.spacing(6),
    marginBottom: theme.spacing(2),
    width: theme.spacing(6),
  },
  statCard: {
    alignItems: 'center',
    display: 'flex',
    flexFlow: 'column nowrap',
    justifyContent: 'center',
  },
  teacherDetails: {
    display: 'block',
  },
  teacherHeader: {
    alignItems: 'center',
    display: 'flex',
    justifyContent: 'space-between',
    width: '100%',
  },
  teacherUsername: {
    marginRight: theme.spacing(2),
    minWidth: theme.spacing(12),
  },
  teacherName: {
    flexGrow: '1',
  },
}))

const TeacherStats = ({ teacher }) => {
  const classes = useStyles()

  const addItems = (itemsByCourse) => Object.values(itemsByCourse).flat().length

  const StatCard = ({ icon, title, value }) => (
    <Grid item xs={12} md={6} lg={4}>
      <Card variant='outlined'>
        <CardContent className={classes.statCard}>
          <Avatar className={classes.statAvatar}>{icon}</Avatar>
          <Typography variant='h5'>{title}</Typography>
          <Typography variant='h6'>{value}</Typography>
        </CardContent>
      </Card>
    </Grid>
  )

  return (
    <div>
      <Typography variant='h6' gutterBottom>
        Teacher Stats
      </Typography>
      <Grid container spacing={2}>
        <StatCard
          icon={<Assignment fontSize='large' />}
          title='Assignments'
          value={addItems(teacher.tasks)}
        />
        <StatCard
          icon={<ShoppingCart fontSize='large' />}
          title='Online Purchases'
          value={addItems(teacher.purchases)}
        />
        <StatCard
          icon={<Store fontSize='large' />}
          title='Products'
          value={teacher.products.length}
        />
        <StatCard
          icon={<Money fontSize='large' />}
          title='Scores'
          value={addItems(teacher.scores)}
        />
        <StatCard
          icon={<PlaylistAddCheck fontSize='large' />}
          title='Songs'
          value={addItems(teacher.songs)}
        />
        <StatCard
          icon={<LocalAtm fontSize='large' />}
          title='Transactions'
          value={addItems(teacher.transactions)}
        />
      </Grid>
    </div>
  )
}

const TeacherCourses = ({ teacher }) => {
  const StatListItem = ({ icon, primary, secondary }) => (
    <ListItem alignItems='flex-start'>
      <ListItemAvatar>
        <Avatar>{icon}</Avatar>
      </ListItemAvatar>
      <ListItemText primary={primary} secondary={secondary} />
    </ListItem>
  )

  return (
    <div>
      <Typography variant='h6' gutterBottom>
        Courses
      </Typography>
      <Grid container spacing={2}>
        {teacher.courses && teacher.courses.length > 0 ? (
          teacher.courses.map((course, index) => {
            return (
              <Grid item xs={12} md={6} lg={4} key={index}>
                <Card variant='outlined'>
                  <CardContent>
                    <Typography gutterBottom variant='h5'>
                      {course.name}
                    </Typography>
                    <Chip
                      color='secondary'
                      label={`Period ${course.period}`}
                      size='small'
                      style={{ color: 'white', marginRight: '8px' }}
                    />
                    <Chip
                      color='primary'
                      label={`${course.students.length} student${
                        course.students.length === 1 ? '' : 's'
                      }`}
                      size='small'
                      variant='outlined'
                    />
                    <List>
                      <StatListItem
                        icon={<Assignment />}
                        primary='Assignments'
                        secondary={teacher.tasks[course._id].length}
                      />
                      <StatListItem
                        icon={<ShoppingCart />}
                        primary='Online Purchases'
                        secondary={teacher.purchases[course._id].length}
                      />
                      <StatListItem
                        icon={<Money />}
                        primary='Scores'
                        secondary={teacher.scores[course._id].length}
                      />
                      <StatListItem
                        icon={<PlaylistAddCheck />}
                        primary='Song Requests'
                        secondary={teacher.songs[course._id].length}
                      />
                      <StatListItem
                        icon={<LocalAtm />}
                        primary='Transactions'
                        secondary={teacher.transactions[course._id].length}
                      />
                    </List>
                  </CardContent>
                </Card>
              </Grid>
            )
          })
        ) : (
          <Grid item xs={12}>
            <Typography>No courses have been created</Typography>
          </Grid>
        )}
      </Grid>
    </div>
  )
}

const TeacherSettings = ({ settings }) => {
  const SettingValue = ({ value }) => {
    const valueString = JSON.stringify(value)
    const type = typeof value
    if (type === 'boolean')
      return (
        <Chip
          color={value ? 'primary' : 'secondary'}
          label={valueString}
          style={{ color: 'white' }}
        />
      )
    return valueString
  }

  return (
    <div>
      <Typography variant='h6' gutterBottom>
        Settings
      </Typography>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Setting</TableCell>
            <TableCell>Value</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {Object.keys(settings).map((setting, index) => (
            <TableRow key={index}>
              <TableCell>{setting}</TableCell>
              <TableCell>
                <SettingValue value={settings[setting]} />
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  )
}

const TeacherList = ({ teachers }) => {
  const [expanded, setExpanded] = useState(false)

  const classes = useStyles()

  const handleChange = (panel) => (ev, isExpanded) => {
    setExpanded(isExpanded ? panel : false)
  }

  return (
    <>
      {teachers?.length > 0 ? (
        teachers.map((teacher) => {
          return (
            <ExpansionPanel
              expanded={expanded === teacher.username}
              key={teacher.username}
              onChange={handleChange(teacher.username)}
            >
              <ExpansionPanelSummary expandIcon={<ExpandMore />}>
                <div className={classes.teacherHeader}>
                  <Chip
                    color='secondary'
                    label={teacher.username}
                    className={classes.teacherUsername}
                  />
                  <Typography className={classes.teacherName} variant='h5'>
                    {teacher.firstName} {teacher.lastName}
                  </Typography>
                  <TeacherDialog teacher={teacher} />
                  <DeleteTeacherAlert teacher={teacher} />
                </div>
              </ExpansionPanelSummary>
              <ExpansionPanelDetails className={classes.teacherDetails}>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <TeacherStats teacher={teacher} />
                  </Grid>
                  <Grid item xs={12}>
                    <TeacherCourses teacher={teacher} />
                  </Grid>
                  <Grid item xs={12}>
                    <TeacherSettings settings={teacher.settings} />
                  </Grid>
                  <Grid item xs={12}>
                    <Typography>
                      <b>Viewed Changes</b>:{' '}
                      {teacher.viewedChanges ? 'yes' : 'no'}
                    </Typography>
                  </Grid>
                </Grid>
              </ExpansionPanelDetails>
            </ExpansionPanel>
          )
        })
      ) : (
        <Typography>No teachers found</Typography>
      )}
    </>
  )
}

export default TeacherList
