import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { Chip } from '@material-ui/core'
import MaterialTable from 'material-table'

import Points from '../Points'
import PurchaseDetailsList from './PurchaseDetailsList'
import agent from '../../agent'

import { DELETE_PURCHASE } from '../../constants/actionTypes'

const mapStateToProps = (state) => ({
  ...state.common.settings,
  ...state.purchases,
})

const mapDispatchToProps = (dispatch) => ({
  onDelete: (id) =>
    dispatch({
      type: DELETE_PURCHASE,
      payload: agent.Purchases.delete(id),
      snackbar: { message: 'Purchase removed', variant: 'success' },
    }),
})

const ManagePurchasesTable = ({ courses, onDelete, taxRate }) => {
  const [data, setData] = useState([])

  const mappedData = courses
    .map((course) =>
      course.purchases.map((purchase) => ({
        createdAt: new Date(purchase.createdAt).toDateString(),
        note: purchase.note,
        period: course.period,
        products: purchase.products,
        purchasedBy: `${purchase.student.firstName} ${purchase.student.lastName}`,
        quantities: purchase.quantities,
        status: purchase.status,
        totalCost: purchase.products.reduce(
          (prev, cur) =>
            prev +
            cur.cost * purchase.quantities[cur._id] * (taxRate / 100 + 1),
          0
        ),
        totalItems: Object.values(purchase.quantities).reduce(
          (prev, cur) => prev + cur,
          0
        ),
        _id: purchase._id,
      }))
    )
    .flat()

  const [columns] = useState([
    { title: 'Purchase Date', field: 'createdAt', editable: 'never' },
    { title: 'Total Items', field: 'totalItems' },
    {
      title: 'Total Cost',
      field: 'totalCost',
      render: (rowData, renderType) =>
        renderType === 'row' ? (
          <Points value={rowData.totalCost} />
        ) : (
          <Points value={rowData} />
        ),
    },
    { title: 'Purchased by', field: 'purchasedBy' },
    {
      title: 'Period',
      field: 'period',
      editable: 'never',
      render: (rowData, renderType) =>
        renderType === 'row' ? (
          <Chip label={rowData.period} color='secondary' />
        ) : (
          <Chip label={rowData} color='secondary' />
        ),
    },
    { title: 'Notes', field: 'note' },
    { title: 'Status', field: 'status' },
  ])

  useEffect(() => {
    setData(mappedData)
  }, [courses])

  return (
    <MaterialTable
      title='Purchases'
      columns={columns}
      data={data}
      localization={{
        body: {
          emptyDataSourceMessage: 'No purchases have been made',
          editRow: {
            deleteText: 'Delete purchase?',
            saveTooltip: 'Delete purchase',
          },
        },
      }}
      options={{ actionsColumnIndex: -1, paging: false, grouping: true }}
      detailPanel={(rowData) => {
        return (
          <PurchaseDetailsList
            products={rowData.products}
            quantities={rowData.quantities}
          />
        )
      }}
      onRowClick={(event, rowData, togglePanel) => togglePanel()}
      editable={{
        onRowDelete: (oldData) =>
          new Promise((resolve, reject) => {
            const dataDelete = [...data]
            const index = oldData.tableData.id
            dataDelete.splice(index, 1)
            setData([...dataDelete])
            onDelete(oldData._id)
            resolve()
          }),
      }}
    />
  )
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ManagePurchasesTable)
